
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import Uploader from 'vux-uploader'
export default {
name: 'app',
directives: {
TransferDom
},
components: {
XHeader,
ViewBox,
Group,
Cell,
Confirm,
Toast,
Uploader,
Flexbox,
FlexboxItem,
},
data() {
return {
varmax: 1,
images: [],
uploadUrl: 'use/upFile',
autoUpload: true,

head: '',

nickName: '',

wxName: '',

signName: '',

IDName: '15065',

money: '0' + '元',

nameShow: false,

wxShow: false,

signShow: false,

nameToast: false,

success: false,

fail: false,

wxToast: false,

signToast: false
};
},
mounted() {
this.$get("user/getUserInfo", {
field: ["user_name", "user_head", "user_info", "user_weixin"]
},
_res => {
this.nickName = _res.msg.user_name;
this.wxName = _res.msg.user_weixin;
this.signName = _res.msg.user_info;
console.log("ppp", _res);
if(_res.res >= 1) {
localStorage.user_info = JSON.stringify(_res.msg);
return;
}
}
);
var user_info = JSON.parse(localStorage.user_info);
var nickName = user_info.user_name;
var wxName = user_info.user_weixin;
var signName = user_info.user_info;
this.nickName = nickName;
this.wxName = wxName;
this.signName = signName;
},
methods: {
toSetHead() {
console.log("去设置用户头像");

/*
this.$post(
"user/save", {
save: {
user_name: msg,
}
},
res => {
console.log(res);
if(res.res >= 1) {
this.$vux.toast.show({
text: "修改成功",
type: "text",
position: "middle"
});
}
if(res < 0) {
this.$vux.toast.show({
text: "网络错误,请重试",
type: "text",
position: "middle"
});
}

}
);
*/
},

toSetNickName() {
console.log("去设置昵称");
this.nameShow = true;
},

onName(msg) {
var _this = this;
if(msg.length < 2 || msg.length > 16) {
_this.nameToast = true;
return false;
}

this.$post(
"user/save", {
save: {
user_name: msg,
}
},
res => {
console.log(res);
if(res.res >= 1) {
_this.nickName = msg;
localStorage.nickName = msg;
this.$vux.toast.show({
text: "修改成功",
type: "text",
position: "middle"
});
}
if(res < 0) {
this.$vux.toast.show({
text: "网络错误,请重试",
type: "text",
position: "middle"
});
}
}
);
},

toSetWx() {
this.wxShow = true;
},

onWx(msg) {
var _this = this;
if(msg.length > 28) {
_this.wxToast = true;
return false;
}
this.$post(
"user/save", {
save: {
user_weixin: msg,
}
},
res => {
console.log(res);
if(res.res >= 1) {
_this.wxName = msg;
localStorage.wxName = msg
this.$vux.toast.show({
text: "修改成功",
type: "text",
position: "middle"
});
}
if(res < 0) {
this.$vux.toast.show({
text: "网络错误,请重试",
type: "text",
position: "middle"
});
}

}
);
},

toSetSign() {
this.signShow = true;
},

onSign(msg) {
var _this = this;
if(msg.length > 26) {
_this.signToast = true;
return false;
}
this.$post(
"user/save", {
save: {
user_info: msg,
}
},
res => {
console.log(res);
if(res.res >= 1) {
_this.signName = msg;
localStorage.signName = msg
this.$vux.toast.show({
text: "修改成功",
type: "text",
position: "middle"
});
}
if(res < 0) {
this.$vux.toast.show({
text: "网络错误,请重试",
type: "text",
position: "middle"
});
}

}
);
},

back() {}
},
};



